export const GetProfileSelector = (state) =>{
    return state.ProfileData.profile
}
export const GetStatusSelector = (state) =>{
    return state.ProfileData.status
}
export const GetIsMyPageSelector = (state) =>{
    return state.ProfileData.IsMyPage
}
export const GetPostsSelector = (state) =>{
    return state.ProfileData.Posts
}
export const GetOwnNameSelector = (state) =>{
    return state.ProfileData.OwnName
}
export const GetProfileIsFetching = (state) =>{
    return state.ProfileData.IsFetching
}

